/* unplugin-vue-components disabled */import HeadBar from '../components/HeadBar.vue';
import api from '../api/index';
export default {
  name: 'RegisterView',

  data() {
    return {
      iswarning: false,
      errorMessage: "",
      warningText: "",
      email: "",
      code: "",
      userName: "",
      nickName: "",
      pwd: "",
      pwdRepeat: "",
      headerStyle: "Return",
      headerTitle: "Register",
      keyWord: ""
    };
  },

  components: {
    HeadBar
  },
  methods: {
    verifyButton() {
      this.verifyCode();
    },

    verifyCode() {
      var that = this;
      api.emailVerify(that.userName, that.email).then(function (response) {});
    },

    backToPrev() {
      this.$router.back(-1);
    },

    confirmButton() {
      var that = this;
      console.log(this.iswarning);

      if (that.iswarning == false) {
        console.log(1);
        this.registerUser();
      } else {
        ElMessage({
          message: 'Input does not meet requirements',
          type: 'warning'
        });
      }
    },

    registerUser() {
      var that = this;

      if (that.email.length < 1 || that.code.length < 1 || that.userName.length < 1 || that.nickName.length < 1 || that.pwd.length < 1 || that.pwdRepeat.length < 1) {
        ElMessage({
          message: 'You must complete all information!',
          type: 'warning',
          duration: 5000
        });
      } else if (that.pwd.length <= 7) {
        ElMessage({
          message: 'The password must be at least 8 characters!',
          type: 'warning',
          duration: 5000
        });
      } else if (that.pwd != that.pwdRepeat) {
        ElMessage({
          message: 'The two password entries are inconsistent, please enter again',
          type: 'warning',
          duration: 5000
        });
      } else {
        api.register(that.email, that.code, that.userName, that.nickName, that.pwd).then(function (response) {
          if (response.data.status == 200) {
            that.$router.push({
              path: '/'
            });
            ElMessage({
              message: 'Success!',
              type: 'success',
              duration: 5000
            });
          } else {
            that.errorMessage = response.data.msg;
            ElMessage({
              message: that.errorMessage,
              type: 'error',
              duration: 5000
            });
          }
        });
      }
    }

  },

  mounted() {
    this.displayDeskOnly = window.innerWidth > 992;
  }

};